import React from 'react';
let currentId = 0;
class RandomId extends React.Component {
    constructor() {
        super(...arguments);
        this.id = `${this.props.prefix}${currentId++}`;
    }
    render() {
        return this.props.children({ id: this.id });
    }
}
RandomId.defaultProps = {
    prefix: 'random-id-',
};
export { RandomId };
